import React from 'react';
import {
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Button,
    Input,
    InputGroup,
    InputGroupText
} from 'reactstrap';

const SuccessModal = ({ isOpen, onClose, testId, onCreateAnother}) => {
    const testUrl = `${window.location.origin}/questionnaires/${testId}`;

    const copyToClipboard = () => {
        navigator.clipboard.writeText(testUrl)
            .catch(() => alert('   '));
    };

    return (
        <Modal isOpen={isOpen} toggle={onClose}>
            <ModalHeader toggle={onClose}>  </ModalHeader>
            <ModalBody>
                <p>   .</p>
                <p>   :</p>

                <InputGroup className="mb-3">
                    <Input value={testUrl} readOnly />
                    <Button color="secondary" onClick={copyToClipboard}>
                        
                    </Button>
                </InputGroup>

                <a
                    href={testUrl}
                    target="_blank"
                    rel="noopener noreferrer"
                >
                      
                </a>
            </ModalBody>
            <ModalFooter>
                <Button color="primary" onClick={onCreateAnother}>
                       
                </Button>
                <Button color="secondary" onClick={onClose}>
                       
                </Button>
            </ModalFooter>
        </Modal>
    );
};

export default SuccessModal;
